import './common.scss'
import './login.scss'
import './ultils/rem'
import { sayHello } from './hello.ts';
if (module.hot) {
  module.hot.accept('./hello.ts', () => {
    // 这里处理模块更新
    console.log('hello.ts has chged!');
  });
}

import Vue from 'vue'
import Wrapper from "./views/Wrapper.vue";
import axios from 'axios'
import router from './routes/index'

// 引入antd
import Antd from 'ant-design-vue';
import 'ant-design-vue/dist/antd.css';

Vue.use(Antd)

// axios 请求拦截器处理添加请求头 Authorization
axios.interceptors.request.use(config => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers['Authorization'] = 'Bearer ' + token; // 留意这里的 
  }
  return config;
})

// 全局路由跳转
axios.interceptors.response.use(
  response => response,
  error => {
    if (error.response && error.response.status === 500) {
      // 跳转到500页面
      router.push({ name: '500' });
    } else if (error.response && error.response.status === 401) {
      // 跳转到401页面
      router.push({ name: '401' });
    } else if (error.response && error.response.status === 404) {
      // 跳转到404页面
      router.push({ name: '404' });
    } else {
      // 可以在这里处理其他类型的错误或者返回Promise.reject(error)来让后续的catch捕获
      // return Promise.reject(error);
    }
  }
);

new Vue({
  el: '#app',
  router,
  render: h => h(Wrapper),
})
